/* 
 References:
 https://developer.mozilla.org/en-US/docs/Web/API/WebRTC_Statistics_API
 https://stackoverflow.com/questions/63152869/detect-if-mediastreamtrack-is-black-blank
*/
import {GLOBAL} from './GLOBAL.js';

export default class RTPStatistics {

  constructor(RTCpeer, interval, showStat){
    this._peer = RTCpeer;
//    console.log ('RTPStatistics RTCPeer', this._peer);
    this._interval = interval;
    this._showStat = showStat; // callback

    this._intervalId = undefined;
    this._previousBytesSent = {};
    this._previousBytesRcvd = {};
    this._previousTimeStamp = Date.now(); // in milli seconds from 1970
    this._currentTimeStamp = Date.now();

    this._getStats = this._getStats.bind(this);

    this.start = this.start.bind(this);
    this.stop = this.stop.bind(this);
    this.export = this.export.bind(this);
  }

  start() {
    if (!this._peer) { console.error ('no RTC Peer'); return false; }

    try {
      this._intervalId = setInterval(this._getStats, this._interval*1000);
    } catch(e) {console.error(e);}
  }

  stop() {
    try {
      clearInterval(this._intervalId);
    } catch(e) {console.error(e);}
  }

  export() {
    try {
      const blob = new Blob (
         GLOBAL.RTPStatistics, 
         {type: 'data:text/json;charset=utf8'}
      );

      const name = 'RTPStatistics';
      const extension = 'json';
      const filename = `${Date.now()}_${name}.` + extension;
 
      const saveLink = document.createElement("a");
      saveLink.href = URL.createObjectURL(blob);
      saveLink.download = filename;
      document.body.appendChild(saveLink);
      saveLink.click();
      document.body.removeChild(saveLink);

      GLOBAL.RTCStatistics = [];

    } catch(e) {console.error(e);}
  }

  async _getStats() {
 

    try {
      const stats = await this._peer.getStats(); 
//      console.log('getStats raw stats\n', stats);

      stats.forEach (report => {
//        console.log('getStats raw report\n', JSON.stringify(report));

        this._currentTimeStamp = report.timestamp;
        const id = report.id.toString();

        if (report.type === 'outbound-rtp') {
          const currentBytesSent = report.bytesSent;

          if (this._previousBytesSent[id] === undefined)
              this._previousBytesSent[id] = 0;

           const prev = this._previousBytesSent[id];
           const kbps = (currentBytesSent - prev)*8
               / (this._currentTimeStamp - this._previousTimeStamp);

           this._previousBytesSent[id] = currentBytesSent;

           const stat = {
             timeStamp: this._currentTimeStamp, 
             reportType: report.type,
             SSRC: report.ssrc.toString(16),
             kind: report.kind,
             kbps: kbps,
           };
           GLOBAL.RTPStatistics.push(stat);
        }

        if (report.type === 'inbound-rtp'){

          const currentBytesRcvd = report.bytesReceived;

          if (this._previousBytesRcvd[id] === undefined)
              this._previousBytesRcvd[id] = 0;

           const prev = this._previousBytesRcvd[id];
           const kbps = (currentBytesRcvd - prev)*8
               / (this._currentTimeStamp - this._previousTimeStamp);

           this._previousBytesRcvd[id] = currentBytesRcvd;

           const stat = {
             timeStamp: this._currentTimeStamp, 
             reportType: report.type,
             SSRC: report.ssrc.toString(16),
             kind: report.kind,
             kbps: kbps,
           };

           GLOBAL.RTPStatistics.push(stat);
         } // end if

       }); // for Each

     } catch (e) { console.error(e);}

    this._previousTimeStamp = this._currentTimeStamp;  
    this._showStat();

  } // end getStats()

};

